dth: 767px) {\n      display: flex;\n      width: 18px;\n      height: 12px;\n    }\n  }\n\n  &__menu-ico {\n    width: 100%;\n    height: 100%;\n    color: $blue;\n  }\n\n  &__nav {\n    width: 100%;\n    max-width: 610px;\n    padding: 17px 0;\n    display: flex;\n    align-items: center;\n    flex-wrap: wrap;\n\n    @media(max-width: 991px) {\n      max-width: calc(100% - 180px);\n    }\n\n    @media(max-width: 767px) {\n      display: none;\n    }\n  }\n\n  &__nav-link {\n    font-size: 15px;\n    text-decoration: none;\n    display: block;\n    color: $primary;\n    font-weight: 300;\n    margin-right: 13px;\n\n    @media(max-width: 991px) {\n      font-size: 14px;\n      margin-right: 10px;\n    }\n\n    &--tablet-link {\n      display: none;\n      @media(max-width: 991px) {\n        display: block;\n      }\n    }\n\n    &:hover {\n      color: $blue;\n    }\n\n    &:last-child {\n      margin: 0;\n    }\n  }\n\n  &__cart {\n    width: 100%;\n    max-width: 180px;\n    position: relative;\n\n    @media(max-width: 767px) {\n      max-width: 40px;\n    }\n  }\n\n  &__cart-heading {\n    position: relative;\n    display: flex;\n    align-items: center;\n    padding: 15px 0 15px 50px;\n    cursor: pointer;\n    @media(max-width: 767px) {\n      padding: 0;\n      width: 40px;\n      height: 45px;\n    }\n  }\n\n  &__cart-val {\n    position: absolute;\n    left: 5px;\n    top: calc(50% - 13px);\n\n    @media(max-width: 767px) {\n      left: 0;\n    }\n  }\n\n  &__cart-ico {\n    width: 26px;\n    height: 26px;\n    fill: $blue;\n\n    @media(max-width: 767px) {\n      width: 20px;\n      height: 20px;\n    }\n  }\n\n  &__cart-count {\n    color: #fff;\n    font-size: 10px;\n    font-weight: 300;\n    line-height: 1;\n    border-radius: 50%;\n    background: $red;\n    width: 18px;\n    height: 18px;\n    text-align: center;\n    position: absolute;\n    left: 15px;\n    top: -2px;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;