;\n    }\n  }\n\n  &__contacts {\n    padding: 0 0 0 35px;\n    width: 100%;\n    max-width: 240px;\n    margin: 6px 0 0 10px;\n    position: relative;\n    @media(max-width: 991px) {\n      margin-left: auto;\n      padding-left: 30px;\n    }\n    @media(max-width: 575px) {\n      margin: 15px 0 0;\n      padding: 0;\n      text-align: center;\n      max-width: 100%;\n    }\n  }\n\n  &__contacts-ico {\n    fill: $secondary;\n    width: 20px;\n    height: 20px;\n    position: absolute;\n    top: 2px;\n    left: 0;\n    @media(max-width: 991px) {\n      width: 18px;\n      height: 18px;\n    }\n    @media(max-width: 575px) {\n      display: none;\n    }\n  }\n\n  &__contact-city {\n    color: $primary;\n    font-size: 15px;\n    font-weight: 300;\n    margin-bottom: 5px;\n    @media(max-width: 991px) {\n      font-size: 14px;\n    }\n  }\n\n  &__contact-phone {\n    display: block;\n    color: $primary;\n    font-size: 20px;\n    font-weight: 700;\n    @media(max-width: 991px) {\n      font-size: 18px;\n    }\n  }\n}\n","$primary: #484848;\n$secondary: #a2a1a1;\n$blue: #a993dc;\n$red: #ef726b;\n$green: #7dca3f;\n$brown: #d88861;\n$yellow: #ffad0d;\n$shadow: rgb(0, 0, 0, .10);\n$borderColor: #e6e6e6;\n\n@mixin placeholder($color) {\n  &::-moz-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &::-webkit-input-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &:-ms-input-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &::-ms-input-placeholder {\n    color: $color;\n    opacity: 1;\n  }\n  &::placeholder {\n    color: $color;\n    opacity: 1;\n  }\n}"],"sourceRoot":""}]);
